.App {
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #282c34;
  min-height: 100vh;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

.App-header h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.mode-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
}

.mode-toggle button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.mode-toggle button:hover {
  background-color: #0056b3;
}

.controls-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.emotion-btn {
  padding: 12px 24px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.emotion-btn:hover {
  background-color: #218838;
}

.ar-window {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80vw;
  max-width: 400px;
  height: 60vh;
  max-height: 300px;
  border: 2px solid #007bff;
  border-radius: 10px;
  overflow: hidden;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.1);
}